.fv-dashboard-stats-wrapper{background:#fafafa;display:flex;height:100px;margin:15px -12px 15px -12px;border-top:1px solid #eee;border-bottom:1px solid #eee}.fv-dashboard-stats-wrapper .fv-total-entry{width:50%;text-align:center;border-right:1px solid #eee;display:flex;flex-direction:column;align-items:center;justify-content:center}.fv-dashboard-stats-wrapper .fv-total-entry .entries-text{font-weight:600;font-size:24px}.fv-dashboard-stats-wrapper .fv-total-entry .duration-text{font-size:20px;font-weight:400}.fv-dashboard-stats-wrapper .fv-stats{width:50%;text-align:center;display:flex;justify-content:center;align-items:center}.fv-dashboard-stats-wrapper .fv-stats>div{display:flex;justify-content:center;align-items:center;height:100%;flex-direction:row}.fv-dashboard-stats-wrapper .fv-stats .stats-up{height:50px}.fv-dashboard-stats-wrapper .fv-stats .stats-down{height:50px}.fv-dashboard-stats-wrapper .fv-stats .fv-status-up-text{position:relative;font-size:18px;color:#28a745}.fv-dashboard-stats-wrapper .fv-stats .fv-status-down-text{position:relative;font-size:18px;color:#dc3545}.fv-dashboard-stats-wrapper .fv-stats .fv-no-stats-data{position:relative;font-size:14px;font-weight:400}
